/* Default input styling */
.text-input {
    border: 1px solid #ccc;
    outline: none;
    transition: border-color 0.3s ease;
}

/* Red border for incorrect input */
.text-input.shake {
    border-color: red;
}

/* Shake animation */
@keyframes shake {
    0%, 100% {
        transform: translateX(0);
    }
    20%, 60% {
        transform: translateX(-10px);
    }
    40%, 80% {
        transform: translateX(10px);
    }
}

.text-input.shake {
    animation: shake 0.5s;
}
